const initialState = {
    name: "Alpha Lau",
    isDarkMode: false,
    loading: true
}

const rootReducer = (state = initialState, action)=>{
    switch(action.type) {
        case 'CHANGE_NAME':
            return {
                ...state,
                name: action.name
            }
        case 'TOGGLE_DARK_MODE':
            return {
                ...state,
                isDarkMode: !action.isDarkMode,
            }
        case 'TOGGLE_LOADING':
            return {
                ...state,
                loading: action.loading
            }
        default:
            return state
    }
}

export default rootReducer