import React, { Component } from 'react';
import {  BrowserRouter, Route, Switch} from 'react-router-dom'
import { TransitionGroup, CSSTransition } from 'react-transition-group'
import { connect } from 'react-redux'

import Home from './component/Home/Home'
import Navbar from './component/Navbar/Navbar';
import Profile from './component/Profile/Profile';
import Game from './component/Game/Game';
import Project from './component/Project/Project';
import Contact from './component/Contact/Contact';

import './App.scss'

class App extends Component {
  render() {
    return (
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Route render={({location})=>(
          <div>
            <div className={this.props.loading?"loading":"loading-end"}>
              <div className="lds-hourglass"></div>
            </div>
            <TransitionGroup>
              <CSSTransition timeout={300} classNames="pageSlider" key={location.key}>
                <div>
                  <Navbar></Navbar>
                  <Switch>
                    <Route exact path='/' component={Home}></Route>
                    <Route exact path='/my_web' component={Home}></Route>
                    <Route path='/my_web/Profile' component={Profile}></Route>
                    <Route path='/my_web/Game' component={Game}></Route>
                    <Route path='/my_web/Project' component={Project}></Route>
                    <Route path='/my_web/Contact' component={Contact}></Route>
                  </Switch>
                </div>
              </CSSTransition>
            </TransitionGroup>
          </div>
        )}>
        </Route>
      </BrowserRouter>
    );
  }
}

const mapStateToProps = (state)=>{
  return {
    loading: state.loading
  }
}

export default connect(mapStateToProps)(App);
